.review {
  padding-bottom: 72px;
  background: $background;
  @media (min-width: 768px) {
    padding-bottom: 100px;
  }
  @media (min-width: 1200px) {
    padding-bottom: 128px;
  }

  &__subtitle {
    color: $blacktext;
    text-align: center;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; /* 133.333% */
  }
  &__text {
    color: rgba(17, 17, 17, 0.7);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.28px;
    text-align: center;
    align-items: center;
    margin: 24px auto 0;
  }
  &__item {
    width: 100%;
    height: 200px;
    padding: 56px 35px 42px;
    flex-shrink: 0;
    position: relative;
    border-radius: 15px;
    border: 1px solid rgba(17, 17, 17, 0.1);
    background: $white;
    box-shadow: 0px 9.049px 26.572px 0px rgba(215, 228, 249, 0.25);
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    color: $blacktext;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.72px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 74px;
    @media (min-width: 768px) {
      margin-bottom: 50px;
    }
    @media (min-width: 1200px) {
      margin-bottom: 91px;
    }
  }
  &__span {
    color: $orange;
  }
  &__wrap--img {
    position: absolute;
    top: -20%;
    left: 50%;
    transform: translateX(-50%);
  }

  &__slider {
    position: relative;
    overflow: hidden;
    height: 275px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    @media (min-width: 375px) {
      width: 335px;
    }
    @media (min-width: 768px) {
      width: auto;
    }
  }
  &__btn {
    color: $blacktext;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
    border: none;
    border: 1px solid $orange;
    padding: 12px 28px;
    background: transparent;
    display: block;
    margin: 32px auto 0;
    @media (min-width: 768px) {
      font-size: 18px;
      padding: 14px 40px;
      margin: 50px auto 0;
    }
  }
}
.swiper-pagination {
  bottom: 0;
}
